import uniPopup  from "@/components/uni-popup/uni-popup"
export default{
	components: {
		uniPopup,
	},
	data(){
		return{
			
		}
	},
	methods:{
		//规则公告
		ruleClick(){
			uni.navigateTo({
				url:'rule'
			})
		},
		//清除缓存
		removeClick(){
			var that = this
			uni.showLoading({
				title:'正在清理...',
			})
			setTimeout(function(){
				uni.hideLoading()
				uni.showToast({
					title:'清理成功',
					icon:'none'
				})
			},1500)
		},
		//检查更新
		versionsClick(){
			uni.showLoading({
				title:'正在检查...',
			})
			setTimeout(function(){
				uni.hideLoading()
				uni.showToast({
					title:'已是最新版本',
					icon:'none'
				})
			},1500)
		},
		//登录弹出
		outloginSharClick(){
			this.$refs.popup.open()
		},
		//登录关闭
		closePopupsSharClick(){
			this.$refs.popup.close()
		},
	}
}
